.text-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .image-container {
    flex-basis: 40%;
  }
  
  .image-container img {
    width: 100%;
    max-width: 200px;
    border-radius: 50%;
    margin-left: 15%;
  }
  
  .text-content {
    flex-basis: 60%;
    margin-right: 20%;
  }
  